<template>
  <div class="login-container">
    <div class="login-header">
      <!-- <div class="header-box">
        <h1>谱创智管</h1>
      </div> -->
    </div>

    <div class="login-main">
      <div class="login-box">
        <el-form ref="loginForm" class="login-form"
          :model="loginForm"
          :rules="loginRules">
          <h3 class="title">登录</h3>

          <el-form-item prop="username" class="form-item">
            <span class="svg-container">
              <svg-icon icon-class="user"></svg-icon>
            </span>
            <el-input v-model="loginForm.username"
              name="username"
              type="text"
              placeholder="输入用户名" />
          </el-form-item>

          <el-form-item prop="password" class="form-item">
            <span class="svg-container">
              <svg-icon icon-class="password" />
            </span>
            <el-input v-model="loginForm.password"
              name="password"
              :type="pwdType"
              placeholder="输入密码"
              @keyup.enter.native="handleLogin" />
            <span class="show-pwd" @click="showPwd">
              <svg-icon :icon-class="pwdType === 'password' ? 'eye' : 'eye-open'" />
            </span>
          </el-form-item>

          <el-form-item>
            <el-button :loading="loading" 
              style="width:100%;"
              type="primary"
              @click.native.prevent="handleLogin">
              <span>登录</span>
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data() {
    const validateUsername = (rule, value, callback) => {
      if (this.users.includes(value)) callback()
      callback(new Error('请使用测试用户名登录'))
    }
    const validatePass = (rule, value, callback) => {
      if (value === '123456') callback()
      callback(new Error('测试密码错误'))
    }
    return {
      loginForm: { 
        username: '', 
        password: '' 
      },
      loginRules: {
        username: [
          {
            required: true,
            trigger: 'blur',
            validator: validateUsername
          }
        ],
        password: [
          {
            required: true,
            trigger: 'blur',
            validator: validatePass
          }
        ],
      },
      loading: false,
      pwdType: 'password',
      redirect: undefined,
      loginType: 'login', // or register
      // users: ['admin', 'djlun', 'test']
      users: ['pczg']
    }
  },
  methods: {
    showPwd() {
      this.pwdType === 'password' ? this.pwdType = 'text' : this.pwdType = 'password'
    },
    handleLogin() {
      this.$refs.loginForm.validate(valid => {
        console.log('valid', valid) // true
        if (valid) {
          this.loading = true
          // setTimeout(() => {
          //   this.loading = false
          //   this.$router.push({ path: '/' })
          // }, 2000)
          console.log('点击登录')
          this.$store.dispatch('Login', this.loginForm).then(() => {
            this.loading = false
            this.$router.push({ path: this.redirect || '/' })
          }).catch(() => {
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
  }
}
</script>

<style lang="scss">
@import 'src/styles/variables';

.login-container {
  .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;
    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
      &:-webkit-autofill {
        box-shadow: 0 0 0px 1000px $loginBg inset !important;
        -webkit-text-fill-color: #fff !important;
      }
    }
  }
  .form-item {
    border: 1px solid #c0c4cc;
    // background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
  }
}
</style>

<style lang="scss" scoped>
@import 'src/styles/variables';
.login-container {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: $loginBg;
  .login-header {
    width: 100%;
    height: 80px;
    .header-box {
      width: 70%;
      margin: auto;
    }
  }
  .login-main {
    height: 320px;
    // background-color: $dark_gray;
    .login-box {
      width: 70%;
      margin: auto;
      .login-form {
        background-color: #fff;
        border-radius: 4px;
        position: absolute;
        left: 0;
        right: 0;
        width: 400px;
        max-width: 90%;
        height: 272px;
        padding: 20px 30px;
        margin: 100px auto;
        .title {
          font-size: 20px;
          font-weight: 400;
          color: $light_gray;
          margin: 0 auto 15px;
          text-align: center;
        }
        .svg-container {
          padding: 6px 5px 6px 15px;
          color: $dark_gray;
          vertical-align: middle;
          width: 30px;
          display: inline-block;
        }
        .show-pwd {
          position: absolute;
          right: 10px;
          top: 7px;
          font-size: 16px;
          color: $dark_gray;
          cursor: pointer;
          user-select: none;
        }
      }
      
    }
  }
  
}
</style>



